import React from "react";
import Nav from "../../components/Navbar/Nav";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Checkbox, Radio } from "antd";
import "./css/AdminProducts.css";
import {FaRupeeSign} from "react-icons/fa"
import { Prices } from "../../components/Prices/Price.js";

const Products = () => {
  const [products, Setproducts] = useState([]);
  const [checked, SetChecked] = useState([]);
  const [radio, SetRadio] = useState([]);
  const [categories, SetCategories] = useState([]);
  const [total, SetTotal] = useState(0);
  const [page, SetPage] = useState(1);
  const [loading, SetLoading] = useState(false);

  // getTotal product
  const GetTotal = async () => {
    try {
      const { data } = await axios.get("/api/admin/product/product-count");
      SetTotal(data?.totalcount);
    } catch (error) {
      console.log(error);
    }
  };


  useEffect(() => {
    const LoadMore = async () => {
      try {
        const { data } = await axios.get(
          `/api/admin/product/product-list/${page}`
        );
        SetLoading(false);
        Setproducts([...products, ...data?.products]);
      } catch (error) {
        SetLoading(false);
        console.log(error);
      }
    };
    if (page === 1) return;
    LoadMore();
  }, [page]);

  //handle Filter
  const HandleFilter = async (value, id) => {
    let all = [...checked];
    if (value) {
      all.push(id);
    } else {
      all = all.filter((c) => c !== id);
    }
    SetChecked(all);
  };
  useEffect(() => {
    //get filtered products
    const FilterProduct = async () => {
      try {
        const { data } = await axios.post("/api/admin/product/filter-product", {
          checked,
          radio,
        });

        Setproducts(data.products);
        // toast.success(data.message);
      } catch (error) {
        toast.error("something went wrong");
      }
    };
    if (checked.length || radio.length) {
      FilterProduct();
    }
  }, [checked, radio]);

  const getCategories = async () => {
    try {
      const { data } = await axios.get("/api/admin/category/get-category");
      if (data?.success) {
        SetCategories(data?.categories);
        toast.success(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("cannot get categories");
    }
  };
  useEffect(() => {
    getCategories();
    GetTotal();
  }, []);

  useEffect(() => {
    const GetAllProducts = async () => {
      try {
        SetLoading(true);
        const { data } = await axios.get(
          `/api/admin/product/product-list/${page}`
          // `/api/admin/product/get-allproducts`
        );
        SetLoading(false);

        if (data?.success) {
          Setproducts(data?.products);
          toast.success(data.message);
        } else {
          toast.error(data.message);
        }
      } catch (error) {
        SetLoading(false);
        console.log(error);
        toast.error("something went wrong");
      }
    };

    GetAllProducts();
  }, []);

  return (
    <>
      <div>
        <Nav />
        <div className="product_main">
          <hr />
          <div className="d-flex row container-fluid p-3 ">
            <div className="col-md-3">
              <AdminMenu />

              <div
                className="categoryFilter_section mt-5 p-3 rounded-4"
                style={{
                  boxShadow:
                    "rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset",
                }}
              >
                <h5 className="text-center text-uppercase  font-monospace  fw-bold ">
                  filter by
                  <span className="text-muted  fw-bold bg-warning ms-2 p-1 rounded-3">
                    {" "}
                    Category
                  </span>{" "}
                </h5>{" "}
                <div
                  className="p-1 py-3  mt-2 rounded-4"
                  style={{
                    boxShadow:
                      "rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px",
                  }}
                >
                  {categories.map((c) => (
                    <>
                      <div className="d-flex  flex-wrap ms-4   " key={c._id}>
                        <Checkbox
                          onChange={(e) => {
                            HandleFilter(e.target.checked, c._id);
                          }}
                          className="fw-bold"
                        >
                          {c.name}
                        </Checkbox>
                      </div>
                    </>
                  ))}
                </div>
              </div>
              <div
                className="priceFilter_section mt-4 m-2 p-3 rounded-4"
                style={{
                  boxShadow:
                    "rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset",
                }}
              >
                <h5 className="text-center text-uppercase  font-monospace  fw-bold mb-2">
                  filter by{" "}
                  <span className="text-muted  fw-bold  bg-warning ms-1  p-1 rounded-3">
                    {" "}
                    price-range
                  </span>{" "}
                </h5>{" "}
                <div
                  className="py-3 rounded-4"
                  style={{
                    boxShadow:
                      "rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px",
                  }}
                >
                  <Radio.Group onChange={(e) => SetRadio(e.target.value)}>
                    {Prices?.map((p) => (
                      <>
                        <div
                          className="d-flex row flex-wrap ms-4  "
                          key={p._id}
                        >
                          <Radio className="fw-bold" value={p.array}>
                            {p.name}
                          </Radio>
                        </div>
                      </>
                    ))}
                  </Radio.Group>{" "}
                  <button
                    className="btn btn-primary p-1 ms-5 text-center px-4 text-capitalize"
                    // onClick={() => {
                    //   SetRadio("");
                    // }}
                  >
                    clear price filter
                  </button>
                </div>
              </div>
            </div>
            <div className="col-md-9 ps-5  ">
              <div className=" p-3 w-90 ps-5 ">
                <h1 className="fw-bold text-uppercase font-monospace">
                  My products
                  <span
                    className="btn btn-success  p-2 ms-2  px-3 mb-2"
                    onClick={(e) => {
                      window.location.reload();
                    }}
                  >
                    Refresh
                  </span>{" "}
                </h1>
                <div className="d-flex  row gap-4">
                  {products.map((data) => (
                    <>
                      <div
                        className="card p-0"
                        style={{ height: "27rem", width: "22rem" }}
                      >
                        <img
                          src={`/api/admin/product/product-photo/${data._id}`}
                          className="card-img-top   cardimg rounded-2 "
                          alt={data.productName}
                        />
                        <div className="card-body">
                          <h4 className="card-title text-capitalize  text-center fw-bold">{data.productName}</h4>
                          <hr />

                          <p className="card-text h4 "> {data.description}.</p>

                          <h5 className="card-text fw-bold">
                             <FaRupeeSign className="h6 mt-1"/>{data.price}.00 RS/-
                          </h5>

                          <Link
                            className="btn btn-primary fs-4 py-2 px-3"
                            key={data._id}
                            to={`/profile/admin/update-product/${data.slug}`}
                          >
                            EDIT
                          </Link>
                          {/* <NavLink to="/productdetail">
                        <button className="btn btn-primary ms-2 px-">
                          More Details
                        </button>
                      </NavLink> */}
                        </div>
                      </div>
                    </>
                  ))}
                </div>

                <div className="m-2 p-3">
                  <h1> total products :{total}</h1>
                  {products && products.length < total && (
                    <button
                      className="btn btn-success py-3 px-5  h4"
                      onClick={(e) => {
                        e.preventDefault();
                        SetPage(page + 1);
                      }}
                    >
                      {loading ? "loading" : "Load More"}
                    </button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Products;
